<template>
	<view>
		<view v-if="showHeader" class="status" :style="{position:headerPosition,top:statusTop}"></view>
		<view v-if="showHeader" class="header" :style="{position:headerPosition,top:headerTop}">
			<view class="addr"></view>
			<view class="input-box">
				
			</view>
			<view class="icon-btn">
				<view class="icon tongzhi" @tap="toMsg"></view>
				<view class="icon setting" @tap="toSetting"></view>
			</view>
		</view>
		<!-- 占位 -->
		<view v-if="showHeader" class="place"></view>
		<!-- 用户信息 -->
		<view class="user">
			<!-- 头像 -->
			<view class="left">
				<image :src="user.avatar_url" @tap="toSetting"></image>
			</view>
			<!-- 昵称,个性签名 -->
			<view class="right">
				<view class="username" @tap="toLogin">{{user.nickname}}</view>
				<view class="signature" @tap="toSetting" >{{user.level_name}}</view>
				<view class="signature" @tap="toSetting" v-if="!user">点击登陆注册</view>
			</view>
			<!-- 二维码按钮 -->
			<view class="erweima" @tap="toMyQR">
				<view class="icon qr"></view>
			</view>
		</view>
		<!-- VIP banner -->
		<view class="VIP" @click="geoVip">
			<view class="img">
				<image src="/static/img/VIP.png"></image>
			</view>
			<view class="title" v-if="!user.level_name">开通VIP会员</view>
			<view class="title" v-if="user.level_name">{{user.level_name}}</view>
			<view class="tis">会员特权</view>
		</view>
		<!-- 订单-余额 -->
		<view class="order">
			
			<!-- 订单类型 -->
			<view class="list" v-if="orderList">
				<view class="box" @tap="toOrderList('/pages/user/order_list/order_list?status=' + 0)">
					<view class="img">
						<image :src="orderList.status_0.icon" mode=""></image>
					</view>
					<view class="text">{{orderList.status_0.text}}</view>
				</view>
				
				<view class="box" @tap="toOrderList('/pages/user/order_list/order_list?status=' + 1)">
					<view class="img">
						<image :src="orderList.status_1.icon" mode=""></image>
					</view>
					<view class="text">{{orderList.status_1.text}}</view>
				</view>
				
				<view class="box" @tap="toOrderList('/pages/user/order_list/order_list?status=' + 2)">
					<view class="img">
						<image :src="orderList.status_2.icon" mode=""></image>
					</view>
					<view class="text">{{orderList.status_2.text}}</view>
				</view>
				
				<view class="box" @tap="toOrderList('/pages/user/order_list/order_list?status=' + 3)">
					<view class="img">
						<image :src="orderList.status_3.icon" mode=""></image>
					</view>
					<view class="text">{{orderList.status_3.text}}</view>
				</view>
				
				<view class="box" @tap="toOrderList('/pages/user/order_list/order_list?status=' + 4)">
					<view class="img">
						<image :src="orderList.status_4.icon" mode=""></image>
					</view>
					<view class="text">{{orderList.status_4.text}}</view>
				</view>
			</view>
			
			<!-- 余额 -->
		
			<!-- 余额 -->
			<block v-if="wallet.wallet && wallet.wallets">
				<view class="balance-info flex">
					<view class="left flex">
						<view class="box">
							<view class="num">{{user.integral}}</view>
							<view class="text">
								{{wallet.wallets.status_0.text}}
								<image  :src="wallet.wallets.status_0.icon" mode=""></image>
							</view>
						</view>
						<block v-if="wallet.wallet.re == 1">
							<view class="box" @tap="toBalance">
								<view class="num">{{user.money}}</view>
								<view class="text">
									{{wallet.wallets.status_1.text}}
										<image  :src="wallet.wallets.status_1.icon" mode=""></image>
								</view>
							</view>
						</block>
					</view>
					<view class="right">
						<block v-if="wallet.wallet && wallet.wallet.is_wallet == 1">
							<view class="box" @tap="toDeposit">
								<view class="img">
									<view class="icon chongzhi"></view>
								</view>
								<view class="text">
								
								{{wallet.wallets.status_2.text}}
								</view>
							</view>
						</block>
					</view>
				</view>
			</block>
		</view>
		<!-- 工具栏 -->
		<view class="toolbar">
			<view class="title">我的工具栏</view>
			<view class="list">
				<view class="box" v-for="(row,index) in mytoolbarList" :key="index" @tap="toPage(row)">
					<view class="img">
						<image :src="row.icon"></image>
					</view>
					<view class="text">{{row.name}}</view>
				</view>
			</view>
		</view>
		<!-- 占位 -->
		<view class="place-bottom"></view>
	</view>
</template>
<script>

	export default {
		data() {
			return {
				isfirst:true,
				headerPosition:"fixed",
				headerTop:null,
				statusTop:null,
				showHeader:true,
				//个人信息,
				user:{
					username:'游客1002',
					face:'/static/img/face.jpg',
					signature:'点击昵称跳转登录/注册页',
					integral:0,
					balance:0,
					envelope:0
				},
				// 订单类型
				orderList:[],
				// 工具栏列表
				mytoolbarList:[],
				// 钱包模块
				wallet: {}
			}
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
		    setTimeout(function () {
		        uni.stopPullDownRefresh();
		    }, 1000);
		},
		onPageScroll(e){
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
			this.headerTop = e.scrollTop>=0?null:0;
			this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
		},
		
		onLoad() {
			this.statusHeight = 0;
			// #ifdef APP-PLUS
			this.showHeader = false;
			this.statusHeight = plus.navigator.getStatusbarHeight();
			// #endif
			
			this.getUser()
		},
		onReady(){
			//此处，演示,每次页面初次渲染都把登录状态重置
			uni.setStorage({
				key: 'UserInfo',
				data: false,
				success: function () {
				},
				fail:function(e){
				}
			});
		},
		onShow(){
			uni.getStorage({
				key: 'UserInfo',
				success: (res)=>{
					if(!res.data){
						if(this.isfirst){
							//this.toLogin();
						}
						return ;
					}
					// this.user = res.data;
				},
				fail:(e)=>{
					//this.toLogin(); 
				}
			});
		},
		methods: {
			//消息列表
			toMsg(){
				uni.navigateTo({
					url:'../../msg/msg'
				})
			},
			toOrderList(url){
				uni.navigateTo({
					url:url
				})
			},
			toSetting(){
				uni.navigateTo({
					url:'../../user/setting/setting'
				})
			},
			toMyQR(){
				uni.navigateTo({
					url:'../../user/myQR/myQR'
				})
			},
			toLogin(){
				uni.showToast({title: '请登录'});
				uni.navigateTo({
					url:'../../login/login'
				})
				this.isfirst = false;
			},
			isLogin(){
				//实际应用中,用户登录状态应该用token等方法去维持登录状态.
				const value = uni.getStorageSync('UserInfo');
				if (value) {
					return true;
				}
				return false
			},
			toDeposit(){
				uni.navigateTo({
					url:'../../user/deposit/deposit'
				})
			},
			toPage(row){
				if(!row.url){
					if(row.open_type == 'tel') {
						uni.makePhoneCall({
						    phoneNumber: '10086' 
						});
					}
					uni.showToast({title: '模板未包含此页面'});return;
				}
				uni.navigateTo({
					url:row.url
				})
			},
			
			// 用户中心数据
			getUser() {
				this.$request({
					url: this.$api.user.index
				}).then(res => {
					console.log(res);
					this.orderList = res.data.orders;
					this.mytoolbarList = res.data.menus;
					this.user = res.data.user_info;
					this.wallet.wallet = res.data.wallet;
					this.wallet.wallets = res.data.wallets;
					console.log(this.orderList);
				})
			},
			// 会员
			geoVip() {
				uni.navigateTo({
					url:'/pages/member/member'
				})
			},
			
			toBalance(){
				uni.navigateTo({
					url:'/pages/balance/balance'
				})
			}
		}
	} 
</script>
<style lang="scss">
	page{position: relative;background-color: #fff;}
	.status {
		width: 100%;
		height: 0;
		position: fixed;
		z-index: 10;
		background-color: #f06c7a;
		top: 0;
		/*  #ifdef  APP-PLUS  */
		height: var(--status-bar-height);//覆盖样式
		/*  #endif  */
		
	}
	
	.header{
		width: 92%;
		padding: 0 4%;
		height: 100upx;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		position: fixed;
		top: 0;
		z-index: 10;
		background-color: #f06c7a;
		/*  #ifdef  APP-PLUS  */
		top: var(--status-bar-height);
		/*  #endif  */
		.icon-btn{
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;
			.icon{
				color: #fff;
				width: 60upx;
				height: 60upx;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				font-size: 42upx;
			}
		}
	}
	.place{
		background-color: #f06c7a;
		height: 100upx;
		/*  #ifdef  APP-PLUS  */
		margin-top: var(--status-bar-height);
		/*  #endif  */
	}
	.place-bottom{
		height: 300upx;
	}
	.user{
		width: 92%;
		padding: 0 4%;
		display: flex;
		align-items: center;
		// position: relative;
		background-color: #f06c7a;
		padding-bottom: 120upx;
		.left{
			width: 20vw;
			height: 20vw;
			flex-shrink: 0;
			margin-right: 20upx;
			border: solid 1upx #fff;
			border-radius: 100%;
			image{
				width: 20vw;
				height: 20vw;
				border-radius: 100%;
			}
			
		}
		.right{
			width: 100%;
			.username{
				font-size: 36upx;
				color: #fff;
			}
			.signature{
				color: #eee;
				font-size: 28upx;
			}
		}
		.erweima{
			flex-shrink: 0;
			width: 10vw;
			height: 10vw;
			margin-left: 5vw;
			border-radius: 100%;
		
			display: flex;
			justify-content: center;
			align-items: center;
			background: linear-gradient(to left, #fbbb37 0%,#fcf0d0 105%);
			.icon{
				color: #7b6335;
				font-size: 42upx;
			}
		}
	}
	.order{
		width: 84%;
		margin: 30upx 4% 30upx 4%;
		padding: 30upx 4% 20upx 4%;
		background-color: #fff;
		box-shadow: 0upx 0upx 25upx rgba(0,0,0,0.1);
		border-radius: 15upx;
		.list{
			display: flex;
			border-bottom: solid 1upx #17e6a1;
			padding-bottom: 10upx;
			.box{
				width: 20%;
				.img{
					width: 100%;
					display: flex;
					justify-content: center;
					.icon{
						font-size: 50upx;
						color: #464646;
					}
					image {
						width: 55rpx;
						height: 55rpx;
					}
				}
				.text{
					width: 100%;
					display: flex;
					justify-content: center;
					font-size: 28upx;
					color: #3d3d3d;
					
				}
			}
		}
		.balance-info{
			display: flex;
			padding: 10upx 0;
			font-size: 10pt;
			.left{
				width: 75%;
				display: flex;
				.box{
					flex: 1;
					font-size: 10pt;
					
					.num{
						width: 100%;
						height: 50upx;
						display: flex;
						justify-content: center;
						align-items: flex-end;
						color: #f9a453;
					}
					.text{
						width: 100%;
						display: flex;
						justify-content: center;
						align-items: center;
						color: #3d3d3d;
						font-size: 28upx;
							image{
								margin-left: 5rpx;
								width: 30rpx;
								height: 30rpx;
							}
					}
				}
			}
			.right{
				border-left: solid 1upx #17e6a1;
				width: 25%;
				.box{
					
					.img{
						width: 100%;
						height: 50upx;
						display: flex;
						justify-content: center;
						align-items: flex-end;
						.icon{
							font-size: 45upx;
							color: #e78901;
						}
					}
					.text{
						width: 100%;
						display: flex;
						justify-content: center;
						font-size: 28upx;
						color: #3d3d3d;
					}
				}
			}
		}
	}
	.VIP{
		width: 84%;
		margin: -65upx auto 20upx auto;
		padding: 30upx 4%;
		background: linear-gradient(to left, #dea96d 0%,#f6d59b 100%);
		box-shadow: 0upx 0upx 25upx rgba(0,0,0,0.2);
		border-radius: 15upx;
		display: flex;
		align-items: center;
		.img{
			flex-shrink: 0;
			width: 60upx;
			height: 60upx;
			image{
				width: 60upx;
				height: 60upx;
			}
		}
		.title{
			width: 100%;
			color: #796335;
			font-size: 30upx;
		}
		.tis{
			width: 100%;
			display: flex;
			justify-content: flex-end;
			color: #fcf0d0;
			font-size: 26upx;
		}
	}
	.toolbar{
		width: 92%;
		margin: 0 4% 0 4%;
		padding: 0 0 20upx 0;
		background-color: #fff;
		box-shadow: 0upx 0upx 25upx rgba(0,0,0,0.1);
		border-radius: 15upx;
		.title{
			padding-top: 10upx;
			margin: 0 0 10upx 3%;
			font-size: 30upx;
			height: 80upx;
			display: flex;
			align-items: center;
		}
		.list{
			display: flex;
			flex-wrap: wrap;
			.box{
				width: 25%;
				margin-bottom: 20upx;
				.img{
					width: 23vw;
					height: 10.5vw;
					display: flex;
					justify-content: center;
					
					image{
						width: 50rpx;
						height: 50rpx;
					}
				}
				.text{
					width: 100%;
					display: flex;
					justify-content: center;
					font-size: 26upx;
					color: #3d3d3d;
				}
			}
		}
	}
</style>
